import {ComponentProps} from 'react';
import clsx from 'clsx';
import {AsProp} from '~/shared/types';
import {classVariants} from '~/utils/class-variants';

interface Props extends ComponentProps<'div'> {
  variant?:
    | 'primary'
    | 'secondary'
    | 'sale'
    | 'tertiary'
    | 'success'
    | 'danger'
    | 'warning'
    | 'info';
  as?: AsProp;
  className?: string;
  width?: 'auto' | 'full';
}

export function Pill({
  variant = 'primary',
  as = 'div',
  className,
  width = 'auto',
  ...props
}: Props) {
  const Component = as ?? 'div';
  return (
    <Component
      className={clsx(
        'py-0.5 px-1.5 rounded-sm text-sm font-medium whitespace-nowrap',
        classVariants(variant, {
          primary: 'bg-black text-white',
          secondary: 'bg-gray-100 contrast-more:bg-gray-300 text-gray-500',
          sale: 'bg-red-100 contrast-more:bg-red-300 text-red-600',
          tertiary: 'text-black bg-transparent',
          success: 'text-white bg-emerald-600 contrast-more:bg-emerald-800',
          danger: 'text-white bg-red-600 contrast-more:bg-red-800',
          warning: 'text-white bg-amber-500 contrast-more:bg-amber-700',
          info: 'text-white bg-sky-600 contrast-more:bg-sky-800',
        }),
        classVariants(width, {
          auto: 'w-auto inline-block',
          full: 'w-full',
        }),
      )}
      {...props}
    />
  );
}
